@import "../../abstracts/index";

.google-button {
  span {
    width: 100%;
  }
}

.jk-divider {
  width: 100%;
  position: relative;
  height: var(--pad-m);
  //background-color: var(--t-color-white);
  background-color: transparent;
  text-align: center;
  display: flex;
  text-transform: uppercase;
  color: var(--t-color-gray-5);
  justify-content: center;
  align-items: center;
  white-space: nowrap;
  @include hg { margin: 16px 0; }
  @include lg { margin: 12px 0; }
  @include md { margin: 8px 0; }
  @include sm { margin: 4px 0; }
  
  &.small {
    margin: 6px 0;
  }
  
  &.tiny {
    margin: 0;
  }
  
  &:before, &:after {
    content: " ";
    display: block;
    width: 100%;
    height: 2px;
    background-color: var(--t-color-gray-6);
  }
  
  &:not(:empty) {
    &:before {
      margin-right: var(--pad-xs);
    }
    
    &:after {
      margin-left: var(--pad-xs);
    }
  }
  
  &.horizontal {
    display: initial;
    width: initial;
    //height: auto; // TODO: fix height of horizontal divider
    border-right: 1px solid var(--t-color-gray-6);
    border-left: 1px solid var(--t-color-gray-6);
    background-color: var(--t-color-gray-6);
    
    &:before, &:after {
      content: none;
    }
  }
}

div[class*=" jk-grid"], div[class^="jk-grid"] {
  display: grid;
}

.jk-form-item {
  position: relative;
  
  > p:first-child { // clarification
    height: var(--pad-xs);
    line-height: var(--pad-xs);
    color: var(--t-color-gray-4);
    font-size: var(--text-s-size);
    position: absolute;
    right: 0;
    
    &::first-letter {
      text-transform: uppercase;
    }
  }
  
  > label:not(.jk-input-checkbox-wrapper) {
    display: block;
    width: 100%;
    font-weight: var(--weight-bold);
    padding: 0;
    text-transform: capitalize;
    line-height: var(--pad-xs);
  }
  
  > label > .jk-select-layout {
    line-height: var(--text-m-height);
    font-weight: var(--weight-normal);
  }
  
  > p:last-child {
    color: var(--t-color-error);
    font-size: var(--text-s-size);
    height: calc(var(--pad-xs) + var(--pad-xs));
    line-height: var(--pad-xs);
    
    &::first-letter {
      text-transform: uppercase;
    }
  }
  
  input {
    width: 100%;
  }
  
  .jk-input-password-wrapper {
    width: 100%;
  }
}

.disabled-input {
  color: var(--t-color-gray-4);
  border-color: var(--t-color-gray-4);
  background-color: var(--t-color-shadow-light);
}

// input textarea
.jk-input-text,
.jk-input-number,
.jk-input-password,
.jk-input-textarea,
.jk-input-file,
.jk-input-files,
.jk-select-layout .jk-select,
.jk-multi-select-layout .jk-select,
.jk-input-date-layout .date-literal-label {
  border: 1px solid var(--t-color-gray-5);
  color: inherit;
  //border-radius: var(--border-radius-hg);
  outline: none;
  box-sizing: border-box;
  font-size: inherit;
  line-height: inherit;
  @include transition(border-color);
  
  &:hover, &:focus, &:active {
    border-color: var(--t-color-gray-4);
  }
  
  &:focus, &:active {
    @extend .elevation-1;
  }
  
  &::-webkit-file-upload-button {
    //visibility: hidden;
  }
  
  &.disabled {
    @extend .disabled-input;
  }
  
  background-color: var(--t-color-shadow-lightest);
}

.jk-select-layout, .jk-multi-select-layout {
  > .jk-select {
    > .input-icon, > .jk-multi-select-selected-icons > .input-icon {
      color: var(--t-color-gray-5);
    }
  }
  
  &:hover, &:focus, &:active, &.focused {
    > .jk-select {
      > .input-icon, > .jk-multi-select-selected-icons > .input-icon {
        color: var(--t-color-gray-4);
      }
    }
  }
  
  &.disabled {
    > .jk-select {
      @extend .disabled-input;
      
      > .input-icon, > .jk-multi-select-selected-icons > .input-icon {
        color: var(--t-color-gray-4);
      }
    }
  }
}

.jk-select-options.disabled {
  @extend .disabled-input;
}

.jk-input-password-wrapper {
  .input-icon {
    color: var(--t-color-gray-5);
  }
  
  &:hover, &:focus, &:active, &.focused {
    .input-icon {
      color: var(--t-color-gray-4);
    }
  }
}

.jk-input-text, .jk-input-number, .jk-input-password, .jk-input-textarea {
  &.extend {
    width: 100%;
  }
}

// input
.jk-input-text, .jk-input-password {
  padding: 4px var(--pad-xt);
}

.jk-input-number {
  padding: 4px 4px 4px var(--pad-xt);
}

.jk-input-text, .jk-input-number, .jk-input-password {
  height: var(--pad-xl);
  
  &::placeholder {
    color: var(--t-color-gray-4);
    font-weight: var(--weight-regular);
    font-size: var(--text-s-size);
    line-height: var(--text-s-height);
  }
}

.jk-input-checkbox-wrapper {
  display: inline-flex;
  cursor: pointer;
  
  input.jk-input-checkbox {
    border: 2px solid var(--t-color-primary);
    left: 0;
    -webkit-appearance: none;
    position: relative;
    cursor: pointer;
    background-color: transparent;
    @include transition(background-color);
    
    &:checked {
      background-color: var(--t-color-primary);
    }
    
    &:after {
      position: absolute;
      opacity: 1;
      top: 0;
      color: var(--t-color-primary-text);
      border: solid var(--t-color-primary-text);
      border-width: 0 3px 3px 0;
      transform: rotate(45deg);
      box-sizing: content-box;
    }
    
    &:checked:after {
      //content: '✓'; // (U+2713)
      //content: '✔'; // '\2714' // (U+2714)
      content: " ";
    }
    
    &:focus-visible:not(:checked) {
      outline: none;
      background-color: rgba(var(--t-color-primary-rgb), 0.6);
      @extend .elevation-1;
      
      &:after {
        content: " ";
      }
    }
    
    margin: 2px 4px !important;
    height: var(--pad-s) !important;
    width: var(--pad-s) !important;
    min-height: var(--pad-s) !important;
    min-width: var(--pad-s) !important;
    
    &:after {
      left: 5px;
      width: 4px;
      height: 10px;
    }
  }
  
  &.disabled {
    color: var(--t-color-gray-4);
    
    input.jk-input-checkbox {
      border-color: var(--t-color-gray-4);
      
      &:checked {
        background-color: var(--t-color-gray-4);
      }
    }
  }
  
