.jk-input-password-wrapper {
  position: relative;
  display: inline-block;
  
  input {
    padding-right: var(--pad-l);
  }
  
  .jk-icon {
    position: absolute;
    top: 4px;
    right: var(--pad-xt);
    cursor: pointer;
  }
}

.jk-input-textarea {
  height: 100%;
  width: 100%;
  padding: var(--pad-t);
  resize: none;
}

.jk-select-options-content {
  > div.jk-popover-content {
    padding: 0;
  }
}

.jk-select-options, .jk-select-options-virtual {
  position: relative;
  background-color: var(--t-color-white);
  width: 100%;
  overflow: auto;
  box-sizing: border-box;
  display: block;
  
  .jk-select-option {
    padding: 0 var(--pad-xt);
    
    .jk-input-checkbox-wrapper {
      margin-right: 4px;
    }
    
    &:not(.disabled) {
      cursor: pointer;
    }
    
    &.disabled {
      color: var(--t-color-gray-4);
      
      input, label {
        cursor: auto;
      }
    }
    
    &:hover:not(.disabled) {
      background-color: var(--t-color-gray-6);
    }
    
    &.selected {
      background-color: var(--t-color-primary);
      color: var(--t-color-white);
      
      &:hover:not(.disabled) {
        background-color: var(--t-color-primary-light);
        
        .jk-input-checkbox {
          background-color: var(--t-color-primary-light);
        }
      }
      
      &.disabled {
        background-color: rgba(var(--t-color-primary-rgb), 0.4);
      }
      
      .jk-input-checkbox {
        border: 1px solid var(--t-color-white);
      }
    }
  }
}

.jk-select-options {
  max-height: 256px;
}

.jk-select-layout, .jk-multi-select-layout {
  position: relative;
  
  &.extend {
    width: 100%;
  }
  
  .jk-select {
    padding: 0 4px 0 var(--pad-xt);
    position: relative;
    text-align: start;
    
    > .jk-icon {
      position: absolute;
      right: 2px;
      top: 0;
    }
    
    .jk-multi-select-selected-options {
      gap: 4px;
      padding: 4px;
      
      .jk-tag {
        gap: 4px;
        background-color: var(--t-color-shadow-light);
        
        .jk-icon:hover {
          color: var(--t-color-gray-2);
          cursor: pointer;
        }
      }
    }
  }
  
  &.open {
    .jk-select {
      border-color: var(--t-color-gray-4);
      
      > .jk-icon {
        color: var(--t-color-gray-4);
      }
    }
  }
}

.jk-multi-select-layout .jk-select {
  min-height: 34px;
}

.jk-input-toggle-wrapper {
  --input-toggle-height: var(--pad-m);
  --input-toggle-color: var(--t-color-primary);
  --input-toggle-text-color: var(--t-color-primary-text);
  position: relative;
  height: var(--input-toggle-height);
  display: flex;
  align-items: center;
  
  &.disabled {
    --input-toggle-color: var(--t-color-gray-6);
  }
  
  > p {
    display: inline-block;
  }
  
  input {
    opacity: 0;
    width: 0;
    height: 0;
    margin: 0;
    
    &:checked + .jk-input-toggle-slider {
      background-color: var(--input-toggle-color);
    }
    
    &:focus + .jk-input-toggle-slider {
      box-shadow: 0 0 1px var(--input-toggle-color);
    }
    
    &:checked + .jk-input-toggle-slider:before {
      transform: translateX(20px);
    }
  }
  
  &.tiny {
    --input-toggle-height: var(--pad-xs);
  }
  
  &.small {
    --input-toggle-height: var(--pad-s);
  }
  
  &.large {
    --input-toggle-height: var(--pad-l);
  }
  
  .jk-input-toggle-slider {
    margin: 0 var(--pad-xt);
    height: var(--input-toggle-height);
    position: relative;
    width: calc(var(--input-toggle-height) + 20px);
    display: inline-block;
    cursor: pointer;
    background-color: var(--t-color-shadow-lightest);
    border: 2px solid var(--input-toggle-color);
    @include transition();
    box-sizing: border-box;
    
    &:before {
      position: absolute;
      content: "";
      height: calc(var(--input-toggle-height) - 8px);
      width: calc(var(--input-toggle-height) - 8px);
      left: 0;
      bottom: 0;
      background-color: var(--input-toggle-color);
      border: 2px solid var(--input-toggle-text-color);
      @include transition();
    }
  }
  
  &.rounded .jk-input-toggle-slider {
    border-radius: 12px;
    
    &:before {
      border-radius: 50%;
    }
  }
}

.jk-input-date-layout {
  position: relative;
  
  .date-literal-label {
    padding: 4px var(--pad-t);
    height: var(--pad-xl);
    
    &.placeholder {
      color: var(--t-color-gray-4);
      font-weight: var(--weight-regular);
      font-size: var(--text-s-size);
      line-height: var(--text-s-height);
    }
    
    &.two-lines {
      height: 58px;
    }
  }
  
  .clear-button {
    position: absolute;
    top: 0;
    right: 0;
  }
}

.jk-checkbox-list {
  .jk-input-checkbox-wrapper {
    
    &:hover:not(.disabled) {
      background-color: var(--t-color-gray-6);
    }
  }
}

.date-literal.jk-col.nowrap {
  height: 58px;
}
